import React from 'react'
import Reveal from '@solid-ui-components/Reveal'
import ContentButtons from '@solid-ui-components/ContentButtons'
import WithDefaultContent from '@solid-ui-blocks/WithDefaultContent'


function htmlDecode(input) {
  var doc = new DOMParser().parseFromString(input, "text/html");
  return doc.documentElement.textContent;
}

const FeatureBlock15 = ({ content: { text, buttons, form, images, html } }) => (
  <div className="pt-40 pb-20 bg-white statistics" id="features15">
    <div className="container">
      
      <h2 class=" text-42 h1g text-center mb-40 pt-40">Checkout How Cool the <b>Open/Click</b> Numbers Look</h2>
      <div className="row nb50">
        <div className="col-md-12 mb-20">
          <img src="data:image/jpeg;base64," class="arrow-down img-responsive" />
          <img src="/static/statistics-e3f87a39a8963fa0b6049e3849f92028.jpg" class="img-responsive" />
        </div>
      </div>       

    </div>
  </div>
)

export default WithDefaultContent(FeatureBlock15)
